import { _decorator, Component, AudioClip } from 'cc';
import { ModuleDef } from '../../scripts/ModuleDef';
const { ccclass, property } = _decorator;

@ccclass('wildbanditoAudio')
export class wildbanditoAudio extends Component {
    onLoad() {
    }

    start() {
    }

    private urlArr: string[] = ["sound/bgm_mg", "sound/bgm_fs"];
    private bgmIndex: number;
    playBgm(index: number) {
        if (this.bgmIndex == index) return;
        this.bgmIndex = index;
        tgx.AudioMgr.inst.audio.stopMusic();
        tgx.AudioMgr.inst.audio.playMusicLoop(this.urlArr[index], ModuleDef.SLOTS_WILDBANDITO)
    }

    /** */
    playReelLoop(speedBn: boolean) {
        if (speedBn) {
            tgx.AudioMgr.inst.audio.playEffect(EEFFECT.spinBtn, ModuleDef.SLOTS_WILDBANDITO);
        } else {
            tgx.AudioMgr.inst.audio.playEffect(EEFFECT.reelActiveLoop, ModuleDef.SLOTS_WILDBANDITO);
        }
    }
    /** */
    private playReelEndBn: boolean = false;
    playReelEnd(isSpeed: boolean = false) {
        if (this.playReelEndBn) return;
        this.playReelEndBn = true
        this.scheduleOnce(() => {
            this.playReelEndBn = false;
        }, 0.01)
        if (isSpeed) {
            tgx.AudioMgr.inst.audio.playEffect(EEFFECT.reelStopQuick, ModuleDef.SLOTS_WILDBANDITO);
        } else {
            tgx.AudioMgr.inst.audio.playEffect(EEFFECT.reelStopNormal, ModuleDef.SLOTS_WILDBANDITO);
        }
    }
    /**spawn wild */
    playSpawnWild() {
        tgx.AudioMgr.inst.audio.playEffect(EEFFECT.symWild, ModuleDef.SLOTS_WILDBANDITO);
    }
    /**spawn Scatter */
    playSpawnScatter() {
        tgx.AudioMgr.inst.audio.playEffect(EEFFECT.symScatter, ModuleDef.SLOTS_WILDBANDITO);
    }
    /** */
    playFastspin() {
        tgx.AudioMgr.inst.audio.playEffect(EEFFECT.fastspin, ModuleDef.SLOTS_WILDBANDITO);
    }
    /** */
    playFsFswon() {
        tgx.AudioMgr.inst.audio.playEffect(EEFFECT.fsFswon, ModuleDef.SLOTS_WILDBANDITO);
    }
    /** */
    playFswonIncrease() {
        tgx.AudioMgr.inst.audio.playEffect(EEFFECT.fsFswonIncrease, ModuleDef.SLOTS_WILDBANDITO);
    }
    /** */
    playFsLoadingVox() {
        tgx.AudioMgr.inst.audio.playEffect(EEFFECT.fsLoadingVox, ModuleDef.SLOTS_WILDBANDITO);
    }
    /** */
    playFsReelTransform() {
        tgx.AudioMgr.inst.audio.playEffect(EEFFECT.fsReelTransform, ModuleDef.SLOTS_WILDBANDITO);
    }
    /** */
    playPrizeSmallwin() {
        let arr: string[] = ["", "01", "02", "03", "04", "05", "06", "07"];
        let index: number = Math.floor(Math.random() * arr.length);
        index = index > arr.length - 1 ? arr.length - 1 : index;
        let str: string = EEFFECT.prizeSmallwin + arr[index];
        tgx.AudioMgr.inst.audio.playEffect(str, ModuleDef.SLOTS_WILDBANDITO);
    }
    /** */
    playPrizeMedwinCounting() {
        tgx.AudioMgr.inst.audio.playEffect(EEFFECT.prizeMedwinCounting, ModuleDef.SLOTS_WILDBANDITO, () => {
            tgx.AudioMgr.inst.audio.playEffect(EEFFECT.prizeMedwinEnd, ModuleDef.SLOTS_WILDBANDITO);
        });
    }
    /** */
    prizeMulti(index: number) {
        let arr: string[] = ["01", "02", "03", "04", "05"];
        index = index ? index : 0;
        index = index < 0 ? 0 : index;
        index = index > arr.length - 1 ? arr.length - 1 : index;
        let str: string = EEFFECT.prizeMulti + arr[index];
        tgx.AudioMgr.inst.audio.playEffect(EEFFECT.prizeMultiGunSwitch, ModuleDef.SLOTS_WILDBANDITO, () => {
            tgx.AudioMgr.inst.audio.playEffect(str, ModuleDef.SLOTS_WILDBANDITO);
        });
    }

    /**bigwin */
    private bigwinId: number;
    playBigwin() {
        tgx.AudioMgr.inst.audio.playEffect(EEFFECT.bgm_bigwin_main, ModuleDef.SLOTS_WILDBANDITO).then((value) => {
            this.bigwinId = value;
        });
    }
    /**bigwin end */
    private bigwinEndId: number;
    playBigwinEnd() {
        tgx.AudioMgr.inst.audio.stopEffect(this.bigwinId,EEFFECT.bgm_bigwin_main, ModuleDef.SLOTS_WILDBANDITO);
        tgx.AudioMgr.inst.audio.playEffect(EEFFECT.bgm_bigwin_end, ModuleDef.SLOTS_WILDBANDITO).then((value) => {
            this.bigwinEndId = value;
        });
    }
    stopBigwinEnd() {
        tgx.AudioMgr.inst.audio.stopEffect(this.bigwinEndId,EEFFECT.bgm_bigwin_end, ModuleDef.SLOTS_WILDBANDITO);
    }
    /**totalwin end */
    private totalId: number;
    playTotalwin() {
        tgx.AudioMgr.inst.audio.playEffect(EEFFECT.prizeTotalwinInfobar, ModuleDef.SLOTS_WILDBANDITO, () => {
            tgx.AudioMgr.inst.audio.playEffect(EEFFECT.bgm_totalwin_main, ModuleDef.SLOTS_WILDBANDITO).then((value) => {
                this.totalId = value;
            });;
        });
    }
    /**totalwin end */
    private totalEndId: number;
    playTotalwinEnd() {
        tgx.AudioMgr.inst.audio.stopEffect(this.totalId,EEFFECT.bgm_totalwin_main, ModuleDef.SLOTS_WILDBANDITO);
        tgx.AudioMgr.inst.audio.playEffect(EEFFECT.bgm_totalwin_end, ModuleDef.SLOTS_WILDBANDITO).then((value) => {
            this.totalEndId = value;
        });
    }
    stopTotalwinEnd() {
        tgx.AudioMgr.inst.audio.stopEffect(this.totalEndId,EEFFECT.bgm_totalwin_end, ModuleDef.SLOTS_WILDBANDITO);
    }
}

export enum EEFFECT {
    fastspin = "sound/general_extract/fastspin",//
    fsFswon = "sound/general_extract/fsFswon",//
    fsFswonIncrease = "sound/general_extract/fsFswonIncrease",//
    fsLoadingVox = "sound/general_extract/fsLoadingVox",//
    fsReelTransform = "sound/general_extract/fsReelTransform",//
    prizeSmallwin = "sound/general_extract/prizeSmallwinVox",// +"" 01 02  03  04 05 06 07
    prizeMedwinCounting = "sound/general_extract/prizeMedwinCounting",//
    prizeMedwinEnd = "sound/general_extract/prizeMedwinEnd",//
    prizeMulti = "sound/general_extract/prizeMulti",// +01 02 03 04 05
    prizeMultiGunSwitch = "sound/general_extract/prizeMultiGunSwitch",//
    prizeTotalwinInfobar = "sound/general_extract/prizeTotalwinInfobar",//
    reelActiveLoop = "sound/general_extract/reelActiveLoop",//
    spinBtn = "sound/general_extract/spinBtn",//
    reelStopNormal = "sound/general_extract/reelStopNormal",//
    reelStopQuick = "sound/general_extract/reelStopQuick",//
    symWild = "sound/general_extract/symWild",//spawn wild
    symScatter = "sound/general_extract/symScatter",//spawn scatter
    bgm_bigwin_main = "sound/bgm_bigwin_main",//bigwin
    bgm_bigwin_end = "sound/bgm_bigwin_end",//bigwin
    bgm_totalwin_main = "sound/bgm_totalwin_main",//
    bgm_totalwin_end = "sound/bgm_totalwin_end",//
}
